import config from '@/config.js'
import defaultConfig from './defaultConfig.js'

// 
const mergeConfig = Object.assign({}, defaultConfig, config)

/**
 * 
 * mix: , , importconfig.js
 */
export default {
  // 
  all() {
    return mergeConfig
  },

  // 
  get(key, def = undefined) {
    if (mergeConfig.hasOwnProperty(key)) {
      return mergeConfig[key]
    }
    console.error(`: ${key}`)
    return def
  }
}
