import store from '@/store'
import * as util from '@/utils/util'

/**
 * 
 */
export const checkLogin = () => {
    console.log('store.getters.token', store.getters.token)
    return !!store.getters.token
}


/**
 * 
 */
export const showSuccess = (msg, callback) => {
    uni.showToast({
        title: msg,
        icon: 'success',
        mask: true,
        duration: 1500,
        success() {
          callback && callback()
        }
    })
}


/**
 * 
 */
 export const showError = (msg, callback) => {
    uni.showModal({
      title: '',
      content: msg,
      showCancel: false,
      success(res) {
        callback && callback()
      }
    })
}

/**
 * 
 */
export const showToast = msg => {
    uni.showToast({
      title: msg,
      icon: 'none'
    })
}



/**
 * url
 */
export const navTo = (url, query = {}, modo = 'navigateTo') => {
    if (!url || url.length == 0) {
      return false
    }
    // query
    const queryStr = !util.isEmpty(query) ? '?' + util.urlEncode(query) : ''
    // , navigateTo
    modo === 'navigateTo' && uni.navigateTo({
      url: `/${url}${queryStr}`
    })
    // , redirectTo
    modo === 'redirectTo' && uni.redirectTo({
      url: `/${url}${queryStr}`
    })
    return true
}

